@import '../styles/basic';
.device{
    position: relative;
    width: 80%;
    max-width: 900px;
    z-index: 2;
    margin-top: 60px;
    border-radius: 8px;
    img{
        box-shadow: 0 24px 38px 3px rgba(0,0,0,0.14),
                0 9px 46px 9px rgba(0,0,0,0.12),
                0 11px 15px -7px rgba(0,0,0,0.20);
    }
    
    @include xs-width{
        width: 85%;
        border-radius: 2px;
        margin-top: 20px;
    }
    @include sm-width{
        width: 85%;
        border-radius: 2px;
        margin-top: 20px;
    }
    @include md-width{
        width: 87%;
        border-radius: 4px;
    }
}
@keyframes video2 {
    0%   {transform: translate3d(0px, 0px, 0px)}
    50%  {transform: translate3d(0px, 10px, 0px)}
    100% { transform: translate3d(0px, 0px, 0px)}
}
.topBanner2 {
    position: absolute;
    right: 150px;
    top: 0px;
    width: 260px;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    animation: video2 10s ease-in-out infinite;

    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    transform-style: preserve-3d;
    will-change: transform;
    @include xs-width{
        display: none;
    }
    @include sm-width{
        display: none;
    }
    @include md-width{
        display: none;
    }
}

@keyframes video {
    0%   {transform: translate3d(0px, 0px, 0px)}
    50%  {transform: translate3d(0px, -10px, 0px)}
    100% { transform: translate3d(0px, 0px, 0px)}
}
.topBanner3 {
    position: absolute;
    left: 100px;
    bottom: 20px;
    width: 300px;
    z-index: 3;
    animation: video 10s ease-in-out infinite;

    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    transform-style: preserve-3d;
    will-change: transform;

    box-shadow: 0 24px 38px 3px rgba(0,0,0,0.14),
                0 9px 46px 9px rgba(0,0,0,0.12),
                0 11px 15px -7px rgba(0,0,0,0.20);
    @include xs-width{
        left: 17px;
        bottom: -26px;
        width: 150px;
    }
    @include sm-width{
        left: 17px;
        bottom: -26px;
        width: 240px;
    }
    @include md-width{
        left: 50px;
        bottom: 20px;
        width: 300px;
    }
}
@keyframes video4 {
    0%   {transform: translate3d(0px, -200px, 0px)}
    5%  {transform: translate3d(0px, 43px, 0px)}
    7%  {transform: translate3d(0px, 40px, 0px)}
    70%  {transform: translate3d(0px, 40px, 0px)}
    75%  {transform: translate3d(0px, -200px, 0px)}
    100% {transform: translate3d(0px, -200px, 0px)}
}
.topBanner4 {
    position: absolute;
    max-width: 250px;
    z-index: 2;
    animation: video4 7s ease-out infinite;
    margin-top: 0px;
    border-radius: 3px;
    box-shadow: 0 24px 38px 3px rgba(0,0,0,0.14),
                0 9px 46px 9px rgba(0,0,0,0.12),
                0 11px 15px -7px rgba(0,0,0,0.20);
    @include xs-width{
        display: none;
    }
    @include sm-width{
        display: none;
    }
}
.pic1box{
    position: relative;
    display: block;
    height: 500px;
    @include xs-width{
        height: 300px;
    }
    @include sm-width{
        height: 300px;
    }
    @include md-width{
        height: 400px;
    }
}
.pic1{
    position: absolute;
    right: 10%; 

    width: 45%;
    max-width: 400px;
    padding-top: 50px;

    @include xs-width{
        width: 60%;
    }
    @include sm-width{
        width: 50%;
    }
    @include md-width{
        width: 50%;
    }
}
.pic1bg{
    position: absolute;
    top: 10px;
    left: 10%;

    width: 45%;
    max-width: 400px;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);

    @include xs-width{
        width: 60%;
    }
    @include sm-width{
        width: 50%;
    }
    @include md-width{
        width: 50%;
    }
}