@import '../styles/basic';

.device{
    position: relative;
    width: 80%;
    height: auto;
    max-width: 800px;
    z-index: 2;
    margin-top: 60px;
    margin-right: 50px;
    border-radius: 8px;
    img{
        box-shadow: 0 24px 38px 3px rgba(0,0,0,0.14),
                0 9px 46px 9px rgba(0,0,0,0.12),
                0 11px 15px -7px rgba(0,0,0,0.20);
    }
    
    @include xs-width{
        margin: 30px 0 0 0;
        width: 90%;
    }
    @include sm-width{
        margin: 30px 0 0 0;
        width: 90%;
    }
    @include md-width{
        margin: 60px 0 0 7%;
        width: 80%;
    }
}
@keyframes video2 {
    0%   {transform: translate3d(0px, 0px, 0px)}
    50%  {transform: translate3d(0px, 10px, 0px)}
    100% { transform: translate3d(0px, 0px, 0px)}
}
.topBanner2 {
    position: absolute;
    right: 200px;
    top: 0px;
    width: 200px;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    animation: video2 10s ease-in-out infinite;

    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    transform-style: preserve-3d;
    will-change: transform;
    @include xs-width{
        display: none;
    }
    @include sm-width{
        display: none;
    }
    @include md-width{
        width: 150px;
        right: 10%;
        top: 15%;
    }
}
@keyframes video {
    0%   {transform: translate3d(0px, 0px, 0px)}
    50%  {transform: translate3d(0px, -30px, 0px)}
    100% { transform: translate3d(0px, 0px, 0px)}
}
.topBanner3 {
    position: absolute;
    left: 150px;
    bottom: 0px;
    width: 300px;
    z-index: 3;
    animation: video 10s ease-in-out infinite;

    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    transform-style: preserve-3d;
    will-change: transform;

    border-radius: 8px;
    box-shadow: 0 24px 38px 3px rgba(0,0,0,0.14),
                0 9px 46px 9px rgba(0,0,0,0.12),
                0 11px 15px -7px rgba(0,0,0,0.20);
    @include xs-width{
        display: none;
    }
    @include sm-width{
        border-radius: 4px;
        width: 30%;
        min-width: 150px;
        left: 3%;
        bottom: -30px;
    }
    @include md-width{
        width: 250px;
        left: 70px;
    }
    @include lg-width{
        width: 250px;
        left: 50px;
    }

}

.topBanner4 {
    position: absolute;
    right: 50px;
    top: 30%;
    width: 220px;
    border-radius: 8px;
    box-shadow: 0 24px 38px 3px rgba(0,0,0,0.14),
                0 9px 46px 9px rgba(0,0,0,0.12),
                0 11px 15px -7px rgba(0,0,0,0.20);
    @include xs-width{
        display: none;
    }
    @include sm-width{
        display: none;
    }
    @include md-width{
        display: none;
    }
    @include lg-width{
        width: 140px;
        right: 5%;
    }
}

.topBannerIcon1 {
    position: absolute;
    right: 220px;
    bottom: 60px;
    width: 64px;
}

.sideMenu1{
    position: absolute;
    right: 0px;
    top: -200px;

    width: 80%;
    max-width: 500px;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    @include sm-width{
        position: relative;
        width: 70%;
        top: 0px;
        margin: 0 0 140px 31%;
    }
    @include md-width{
        position: relative;
        width: 70%;
        top: 0px;
        margin: 0 0 200px 36%;
        left: 20px;

    }
}
