@import '../styles/basic';
.test {
    background: burlywood;
    width: 500px;
    height: 500px;
    position: relative;
    text-align: center;
    padding-top: 250px;
}
.topBanner{
    position: relative;
    max-width: 1100px;
    margin: 60px auto 0px auto;
}
.device{
    position: relative;
    width: 60%;
    z-index: 2;
    @include xs-width{
        width: 55%;
        margin: 1% 0 0 3%;
    }
    @include sm-width{
        width: 60%;
        margin: 1% 0 0 3%;
    }
    @include md-width{
        width: 60%;
        margin: 1% 0 0 3%;
    }
    @include lg-width{
        width: 60%;
        margin: 1% 0 0 3%;
    }
}
.wordBox{
    position: absolute;
    width: 40%;
    top: 0px;
    right: 0px;
    font-family: "Inter", sans-serif;
}
@keyframes nvr {
    0%   {transform: scale(0.5); opacity: 0;}
    15%  {transform: scale(1);   opacity: 1;}
    25%  {transform: scale(1);   opacity: 1;}
    90%  {transform: scale(1);   opacity: 1;}
    100% {opacity: 0;}
}
.word1{
    position: absolute;
    font-size: 100px;
    top: 233px;
    right: 21px;
    font-style: italic;
    font-weight: bold;
    z-index: 1;
    animation: nvr 10s ease-in-out infinite;
    @include xs-width{
        font-size: 35px;
        top: 30px;
    }
    @include sm-width{
        font-size: 50px;
        top: 70px;
    }
    @include md-width{
        font-size: 80px;
        top: 200px;
    }
}
@keyframes video {
    0%   {transform: scale(0.5); opacity: 0;}
    20%  {transform: scale(0.5);   opacity: 0;}
    25%  {transform: scale(1);   opacity: 1;}
    90%  {transform: scale(1);   opacity: 1; }
    100% { opacity: 0;}
}
.word3{
    position: absolute;
    font-size: 68px;
    top: 394px;
    right: 105px;
    font-style: italic;
    font-weight: bold;
    z-index: 1;
    animation: video 10s ease-in-out infinite;
    @include xs-width{
        font-size: 18px;
        top: 109px;
        right: 35px;
    }
    @include sm-width{
        font-size: 25px;
        top: 185px;
        right: 32px;
    }
    @include md-width{
        font-size: 45px;
        top: 311px;
    }
}

@keyframes analysis {
    0%   {transform: scale(0.5); opacity: 0;}
    30%  {transform: scale(0.5); opacity: 0;}
    35%  {transform: scale(1);   opacity: 1;}
    90%  {transform: scale(1);   opacity: 1;}
    100% { opacity: 0; }
}
.word4{
    position: absolute;
    font-size: 34px;
    top: 345px;
    right: 275px;
    font-style: italic;
    font-weight: bold;
    z-index: 1;
    animation: analysis 10s ease-in-out infinite;
    @include xs-width{
        font-size: 14px;
        top: 84px;
        right: 79px
    }
    @include sm-width{
        font-size: 20px;
        top: 155px;
        right: 79px;
    }
    @include md-width{       
        font-size: 22px;
        top: 267px;
        right: 218px;
    }
}
@keyframes greenBox1 {
    0%   { opacity: 0;}
    15%  { opacity: 0; transform: scale(1.5);}
    20%  { opacity: 1; transform: scale(1);}
    90%  { opacity: 1;}
    91%  { opacity: 0;}
    100% { opacity: 0;}
  }
.greenBox{
  width: 230px;
  height: 88px;
  border: 3px solid #57bc59;
  animation: greenBox1 10s infinite;
  top: 260px;
  right: 14px;
  position: absolute;
  @include xs-width{
    border: 2px solid #57bc59;
    width: 85px;
    height: 38px;
    top: 36px;
    right: 16px;
    }
    @include sm-width{
        width: 118px;
        height: 49px;
        top: 80px;
        right: 16px;
    }
    @include md-width{
        width: 182px;
        height: 73px;
        top: 220px;
        right: 17px;
    }
}
@keyframes greenBox2 {
    0%   {opacity: 0;}
    25%  {opacity: 0; transform: scale(1.5);}
    30%  {opacity: 1; transform: scale(1);}
    90%  {opacity: 1;}
    91%  {opacity: 0;}
    100% {opacity: 0;}
  }
.greenBox3{
    width: 221px;
    height: 63px;
    border: 3px solid #57bc59;
    animation: greenBox2 10s infinite;
    top: 410px;
    right: 100px;
    position: absolute;
    @include xs-width{
        border: 2px solid #57bc59;
        width: 63px;
        height: 22px;
        top: 110px;
        right: 32px;
    }
    @include sm-width{
        width: 89px;
        height: 32px;
        top: 186px;
        right: 27px;
    }
    @include md-width{
        width: 149px;
        height: 46px;
        top: 319px;
        right: 28px;
    }
  }
  @keyframes greenBox4 {
    0%   { opacity: 0;}
    35%  {opacity: 0; transform: scale(1.5);}
    40%  {opacity: 1; transform: scale(1);}
    90%  {opacity: 1;}
    91%  {opacity: 0;}
    100% {opacity: 0;}
  }
.greenBox4{
    width: 154px;
    height: 42px;
    border: 3px solid #57bc59;
    animation: greenBox4 10s infinite;
    top: 350px;
    right: 270px;
    position: absolute;
    @include xs-width{
        border: 2px solid #57bc59;
        width: 68px;
        height: 21px;
        top: 84px;
        right: 75px;
    }
    @include sm-width{
        width: 93px;
        height: 28px;
        top: 156px;
        right: 74px;
    }
    @include md-width{
        width: 103px;
        height: 32px;
        top: 267px;
        right: 213px;
    }
  }

.pic1box{
    position: relative;
    display: block;
    width: 100%;

}
.pic1{
    width: 30%;

    max-width: 292px;
    padding-top: 128px;
    @include xs-width{
        width: 50%;
        padding-top: 70px;
    }
    @include sm-width{
        width: 50%;
        padding-top: 70px;
        margin-left: -55px;
    }
    @include md-width{
        width: 50%;
        padding-top: 70px;
        margin-left: -55px;
    }

    
}
.analysisPicLeft{
    position: absolute;
    left: 0%;
    top: 30px;
    width: 400px;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    @include xs-width{
        display: none;
    }
    @include sm-width{
        display: none;
    }
    @include md-width{
        display: none;
    }
    @include lg-width{
        display: none;
    }
}
.analysisPicCenter{
    position: relative;
    display: block;
    margin: 0px auto 0 auto;
    width: 400px;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    @include xs-width{
        margin: 0 0 0 1%;
        width: 50%;
    }
    @include sm-width{
        margin: 0 0 0 5%;
        width: 50%;
    }
    @include md-width{
        margin: 0 0 0 5%;
        width: 50%;
    }
    @include lg-width{
        margin: 0 0 0 5%;
    }
}
.analysisPicMain{
    position: absolute;
    top: -130px;
    right: 0%;
    width: 500px;
    @include xs-width{
        right: 20%;
        top: 9%;
        width: 50%;
    }
    @include sm-width{
        right: 15%;
        top: 9%;
        width: 50%;
    }
    @include md-width{
        right: 15%;
        top: 9%;
        width: 50%;
    }
    @include lg-width{
        right: 15%;
        top: -81px;
    }
        
}
.pic1bg{
    position: absolute;
    left: 194px;
    top: 41px;
    width: 600px;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    @include xs-width{
        left: 20px;
        width: calc(100% - 40px);
    }
    @include sm-width{
        left: 0px;
        width: 100%;
    }
    @include md-width{
        left: 10%;
        width: 80%;
    }
    @include lg-width{
        width: 720px;
    }
}
.video1{
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    width: 100%;
    @media only screen and (max-width: 600px) {
        width: 100%;
        max-width: 100%;
    }
}
.VDbg{
    background-image: url(http://winner-fan.com.tw/portfolio/vd-02.png);
    background-position: 50% 50%;
    background-size: cover;
    display: flex;
    justify-content: center;
    margin: 0px;
    position: relative;
    right: 0px;
    width: 100vw;
    height: 550px;
    @include xs-width{
        height: 180px;
    }
    @include sm-width{
        height: 250px;
    }
    @include md-width{
        height: 450px;
    }
    @include lg-width{
        height: 550px;
    }
    .devicePic{
        width: 60%;
        max-width: 900px;
        position: relative;
        @include xs-width{
            width: 100%;
        }
        @include sm-width{
            width: 100%;
        }
        @include md-width{
            width: 90%;
        }
        @include lg-width{
            width: 90%;
        }
        img{
            position: absolute;
            bottom: 0px;
        }
    }
}

.VD-trio{
    display: flex;
    justify-content: center;
    align-items: center;
    .bg{
        position: absolute;
        left: 0;
        top: 164px;
        right: 0;
        bottom: 0;
        background-color: #6ecadc;
    }
}



