@import '../styles/basic';
.device{
    position: relative;
    width: 80%;
    max-width: 800px;
    z-index: 2;
    margin: 60px 0 0 300px;
    border-radius: 8px;
    @include xs-width{
        margin: 20px 0 0 0;
    }
    @include sm-width{
        margin: 20px 0 0 0;
        width: 60%;
    }
    @include md-width{
        margin: 0px;
        width: 70%;
        
    }
    @include lg-width{
        margin: 20px 0 0 0px;
        width: 59%;
    }
    img{
        position: relative;
        z-index: 2;
    }

    &::before, &:after{
        content:"";
        position:absolute; 
        z-index:1;
        background-color: #000000;
        box-shadow:0 0 20px rgba(0,0,0,0.8);
        top:50%;
        bottom:10px;
        left:10px;
        right:10px;
        border-radius:20%;
    }
}



@keyframes video2 {
    0%   {transform: translate3d(0px, 0px, 0px)}
    50%  {transform: translate3d(0px, 10px, 0px)}
    100% { transform: translate3d(0px, 0px, 0px)}
}
.topBanner2 {
    position: absolute;
    left: 150px;
    top: 60px;
    width: 20%;
    // animation: video2 10s ease-in-out infinite;
    z-index: 3;

    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    transform-style: preserve-3d;
    will-change: transform;
    @include xs-width{
        left: 20px;
        top: 34%;
        width: 22%;
    }
    @include sm-width{
        left: 20px;
        top: 27%;
        width: 18%;
    }
    @include md-width{
        left: 40px;
        top: 35%;
        width: 16%;
    }
    @include lg-width{
        left: 14%;
        top: 29%;
        width: 15%;
    }


    img{
        position: relative;
        z-index: 3;
    }

    &::before{
        content:"";
        position:absolute; 
        z-index:1;
        background:radial-gradient(ellipse 60% 10% at 35% 25%,rgba(0,0,0,0.8),rgba(0,0,0,0));
        top:95%;
        bottom:-9%;
        left:29%;
        right:-5%;
    }
    &::after{
        content:"";
        position:absolute; 
        z-index:1;
        background:radial-gradient(ellipse 30% 5% at 35% 25%,rgba(0,0,0,0.8),rgba(0,0,0,0));
        top: 91%;
        bottom: -21%;
        left: 4%;
        right: -5%;
        // content:"";
        // position:absolute; 
        // z-index:1;
        // box-shadow:0 0 20px rgba(0,0,0,0.6);
        // top:95%;
        // bottom:1%;
        // left:22%;
        // right:43%;
        // border-radius:50%;
    }
}