@import './styles/basic';
.indexContent{
    max-width: 1200px;
}

.homeIntroText{
    font-size: 2.5rem;
    line-height: 3.5rem;
    font-weight: 100;
    padding: 10rem 10rem 1.6rem 10rem;
    @include  xs-width{
        font-size: 1.3rem;
        line-height: 2.3rem;
        padding: 8rem 6rem 1.6rem 6rem;
    }
    @include  sm-width{
        font-size: 1.6rem;
        line-height: 2.5rem;
        padding: 8rem 6rem 1.6rem 6rem;
    }
    @include  md-width{
        font-size: 2rem;
        line-height: 3rem;
        padding: 8rem 6rem 1.6rem 6rem;
    }
    @include  lg-width{
        font-size: 2rem;
        line-height: 3rem;
        padding: 8rem 6rem 1.6rem 6rem;
    }
}

.box{
    width: calc(50% - 40px);
    margin: 20px 20px 50px 20px;
    box-sizing: border-box;
    height: 450px;
    display: block;
    position: relative;
    cursor: pointer;
    text-decoration: none;
    border-radius: 0px;
    @include xs-width{
        width: 100%;
        height: 363px;
    }
    @include sm-width{
        width: 100%;
        height: 410px;
    }
    @include md-width{
        height: 335px;
    }
    @include lg-width{
        height: 390px;
    }

    img{
        pointer-events:none;
        position: absolute;
        z-index: 1;
    }
    &:nth-child(odd){
        margin-top: 80px;
    }
}

.project{
    background-color: #1368A0;
    margin: 10px;
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    transition: all .6s;
    &:hover{
        margin: 20px;
        width: calc(100% - 40px);
        height: calc(100% - 40px);
    }
    &.color1{background-color:#13A092; }
    &.color2{background-color: #3198AD;}
    &.color3{background-color: #31AD6A;}
    &.color4{background-color:#AD315C;}
    &.color5{background-color:#D27218;}
}
.projectTitle{
    color: #ffffff;
    position: absolute;
    top: 30px;
    left: 50px;
    width: 80%;
    &.title3{
        top: auto;
        bottom: 30px;
        width: 33%;
    }
    &.title4{
        top: auto;
        bottom: 15px;
        width: 80%;
    }
}
