@import '../styles/basic';
@keyframes video2 {
    0%   {transform: translate3d(0px, 0px, 0px)}
    50%  {transform: translate3d(0px, 10px, 0px)}
    100% { transform: translate3d(0px, 0px, 0px)}
}
.device{
    position: relative;
    width: 90%;
    max-width: 1200px;
    z-index: 2;
    margin-top: 00px;
    margin-left: -150px;
    border-radius: 8px;
    animation: video2 10s ease-in-out infinite;
    @include xs-width{
        width: 100%;
        margin-left: -17%;
    }
    @include sm-width{
        width: 100%;
        margin-left: -17%;
    }
}
@keyframes video2-shadow {
    0%   {opacity: 0.4;}
    50%  {opacity: 1;}
    100% {opacity: 0.4;}
}
.deviceShadow{
    position: absolute;
    width: 90%;
    max-width: 1200px;
    z-index: 2;
    bottom: 0px;
    left: 62px;

    animation: video2-shadow 10s ease-in-out infinite;

    @include xs-width{
        display: none;
    }
    @include sm-width{
        display: none;
    }
    @include md-width{
        display: none;
    }
    @include lg-width{
        display: none;
    }
}

