$circle-R: 75px;
$circle-B: 15px;
.circle{
    box-sizing:border-box;
	width: $circle-R*2;
	height: $circle-R*2;
	// background:transparent;
    position: relative;
    background-color: white;
    border: $circle-B #57D998 solid;
    border-radius: 50%;
	// animation:myRotate 2s  linear forwards;/*forward1*/
	// -webkit-animation:myRotate 2s  linear forwards;
}
.circle:before,.circle:after{
    box-sizing:border-box;
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: $circle-R;
    height: $circle-R*2;
    border: solid;
    border-width:$circle-B 0 $circle-B $circle-B;/**/
    border-color:#576CD9;
    border-radius: $circle-R 0 0 $circle-R;/**/
    background:white;
    z-index: 2;
    margin: -$circle-B;
}
.circle:before{
    transform:rotate(0deg);
    transform-origin:$circle-R $circle-R;
    animation:myRotate-before 18s  linear infinite;
}
.circle:after{
    transform:rotate(180deg);
    transform-origin:$circle-R $circle-R;
}
@-webkit-keyframes myRotate-before {
    0%  {transform:rotate(180deg);}
    6%  {transform:rotate(250deg);}
    33% {transform:rotate(250deg);}
    39% {transform:rotate(238deg);}
    66% {transform:rotate(238deg);}
    72% {transform:rotate(180deg);}
    100%{ transform:rotate(180deg);}
}

