@import './styles/basic';
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 90px;
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;
    // border-bottom: 1px solid #ffffff;
}
.header{
    left: 0px;
    width: 100%;
    height: 4rem;
    display: flex;
    padding: 0 2rem;
    position: fixed;
    transition: top 0.3s;
    align-items: center;
    justify-content: space-between;
      &::before{
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        content: ' ';
        z-index: -1;
        position: absolute;
      }
}
.logo{
    padding: 2rem 0 0 0;
    img{
        width: 60px;
        height: 60px;
    }
}