$xs:420px;
$sm:576px;
$md:768px;
$lg:992px;
$xl:1280px;

//xs
@mixin xs-width() {
    @media all and (max-width: $xs) {
        @content;
    }
}
//sm
@mixin sm-width() {
  @media all and (min-width:$xs) and (max-width: $md) {
        @content;
    }
}
//md
@mixin md-width() {
    @media all and (min-width: $md) and (max-width:$lg) {
         @content;
    }
}
//lg
@mixin lg-width() {
    @media all and (min-width: $lg) and (max-width:$xl) {
         @content;
    }
}
//xl
@mixin xl-width() {
    @media all and (min-width: $xl) {
        @content;
    }
}
#style-1::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

#style-1::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
}

#style-1::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}

.App {
  font-family: sans-serif;
  text-align: center;
}
.listNumber{
  list-style-type: decimal
}

.project-title {
  max-width: 1000px;
  margin: 0 auto;
  @include sm-width{
    display: block;
}
  @include md-width{
    display: block;
  }
}

.menu {
  color: #ffffff;
  display: none;
  @include sm-width{
    display: block;
}
  @include md-width{
    display: block;
  }
}

.side-menu{
  list-style: none;
  padding: 0 0 0 2rem;
  position: sticky;
  top: 4rem;
  & > li{
    margin-bottom: 1.5rem;
  }
  li > ul {
    list-style: none;
    padding-left: 0.875rem;
    li {
      margin: 0 0 0.3rem 0;
    }
    button {
      // font-size: 0.875rem;
    }
  }
}
.side-menu-title {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.scrollTop {
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: opacity 0.4s;
  opacity: 0.5;
}

.scrollTop:hover{
  opacity: 1;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}

.nextBtn{
  display: flex;
  justify-content: center;
  padding: 1rem 0 6rem 0;
  button{
    margin: 0 1rem;
    min-width: 100px;
  }
}


.topBanner{
  position: relative;
  max-width: 1400px;
  margin: 60px auto 40px auto;
  @include xs-width{
    margin: 60px auto 60px auto;
  }
  &.center{
    display: flex;
    justify-content: center;
  }
}
.projectTitle{
  max-width: 1030px;
  margin: 0px auto;
  padding: 0px 50px 0px 50px;
  @include xs-width{
    padding: 0 30px;
  }
  @include sm-width{
    padding: 0 30px;
  }
  @include md-width{
    padding: 0 60px;
  }
  .title{
      padding-bottom: 64px;
      font-weight: 700;
  }
}
.part-title {
  padding-bottom: 0.5rem;
}

.textOnly{
  max-width: 900px;
  margin: 120px auto;
  padding: 0px 32px;
  @media only screen and (max-width: 960px) {
      width: 83.33333333333334%;
      max-width: 712px;
  }
  @media only screen and (max-width: 600px) {
      width: 100%;
      max-width: 100%;
      margin: 64px auto;
  }
  .text{
      font-size: 21px;
      line-height: 36px;
      font-weight: 300;
      font-family: Brown,sans-serif;
      margin-top: 8px;
  }
}

.textPic{
  max-width:1200px;
  margin: 120px auto;
  padding: 0px 32px;
  @media screen and (max-width: 960px) {
      width: 83.33333333333334%;
      max-width: 712px;
  }
  @media only screen and (max-width: 600px) {
      width: 100%;
      max-width: 100%;
      margin: 64px auto;
  }
  .text{
      font-size: 21px;
      line-height: 36px;
      font-weight: 300;
      margin-top: 8px;
      
  }
  &.minusMargin{
    margin: 40px auto -100px auto;
    @media only screen and (max-width: 600px) {
      margin: 40px auto -40px auto;
    }
  }
  &.addPadding{
    padding: 60px 32px;
  }
}

.picBox{
  position: relative;
  display: block;
}

.picOnly{
  max-width: 1136px;
  margin: 0 auto 50px auto;
}
.over100{
  width: 1700px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  right: 50%;
  padding: 30px 0;
  @include xs-width{
    width: 100%;
    right: auto;
  }
  @include sm-width{
    width: 100%;
    right: auto;
  }
  @include md-width{
    width: 100%;
    right: auto;
  }
  &.border-R div{
    border-radius: 4px;
    @include xs-width{
      border-radius: 2px;
    }
    @include sm-width{
      width: 100%;
      right: auto;
    }
  }
  img{
      box-shadow: 0 0 20px rgba(0,0,0,0.5);
      height: 240px;
      margin: 0 24px;
      @include xs-width{
        height: 80px;
        margin: 0 10px;
      }
      @include sm-width{
        height: 120px;
        margin: 0 10px;
      }
      @include md-width{
        height: 160px;
      }
      &.helf{
        height: 120px;
        @include xs-width{
          height: 40px;
        }
        @include sm-width{
          height: 60px;
        }
        @include md-width{
          height: 100px;
        }
      }
  }
}
