$xs:420px;
$sm:576px;
$md:767px;
$lg:992px;
$xl:1280px;

//xs
@mixin xs-width() {
    @media all and (max-width: $xs) {
        @content;
    }
}
//sm
@mixin sm-width() {
  @media all and (min-width:$xs) and (max-width: $md) {
        @content;
    }
}
//md
@mixin md-width() {
    @media all and (min-width: $md) and (max-width:$lg) {
         @content;
    }
}
//lg
@mixin lg-width() {
    @media all and (min-width: $lg) and (max-width:$xl) {
         @content;
    }
}
//xl
@mixin xl-width() {
    @media all and (min-width: $xl) {
        @content;
    }
}